 <template>
        <div class="login">
            <h1>Login</h1>
            <form @submit.prevent="Login" class="form-login">
                <input type="text" placeholder="Email" class="item email" v-model="email" />
                <input type="password" placeholder="Password" class="item" v-model="password"/>
                <input type="submit" value="Login" class="submit"/>
                <p class="account">Need an account? <router-link to="/register" class="register">Register Here</router-link></p>
                <router-link to="/" class="link-google">
                    <div class="image-box">
                        <img src="@/assets/images/icon-google.png" alt="icon-google" />
                    </div>
                    <p class="text-gg">Sign In with Google</p>
                </router-link>
                <router-link to="/" class="links-facebook">
                    <div class="image-box">
                        <img src="@/assets/images/icon-facebook.png" alt="icon-facebook" />
                    </div>
                    <p class="text-gg">Sign In with Facebook</p>
                </router-link>
            </form>
        </div>
</template>

<script>
import '@/assets/styles/Login-Register/LoginForm.css';
import {ref} from 'vue';
import firebase from 'firebase/compat/app';
export default {
    name: 'SignInModal',
    data() {
        return {
           
        };
    },
    setup() {
        const email = ref("");
        const password = ref("");
        
        const Login = () => {
            firebase
                .auth()
                .signInWithEmailAndPassword(email.value, password.value)
                .then(data => console.log(data))
                .catch(err => alert(err.message))
        }
        return{
            Login,
            email,
            password
        }
    },
  methods: {
    
  }
}
</script>

