import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginForm from '@/components/ModalVue/SignInModal.vue'
import Register from '@/components/ModalVue/RegisterModal.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
      path: '/login',
      name: 'SignInModal',
      component: LoginForm,
      // hidden: true
    },
    {
      path: '/register',
      name: 'RegisterModal',
      component: Register,
      // hidden: true
    },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
