<template>
    <div class="slider-accessory">
        <slick :options="myOptions">
            <div class="slide"  v-for="slide in terra" :key="slide.id">
                <div class="card-accessory">
                        <div class="card-slider">
                            <img :src="require(`@/assets/images/flower/accessory/${slide.imageURL}`)" alt="">
                        </div>
                        <div class="card-text">
                            <h4 class="title">{{slide.name}}</h4>
                            <div class="price">
                            <p class="text-prices">{{slide.price}}</p>
                            <p class="text-sale">{{slide.sale}}</p>
                            </div>
                        </div>
                        <div class="card-cart">
                            <div class="icons-button">
                            <router-link to="/" class="link-icons">
                                <font-awesome-icon icon="fa-solid fa-cart-shopping" class="icons"/>
                            </router-link>
                            </div>
                            <div class="icons-button">
                            <router-link to="/" class="link-icons">
                                <font-awesome-icon icon="fa-solid fa-clone" class="icons"/>
                            </router-link>
                            </div>
                            <div class="icons-button">
                            <router-link to="/" class="link-icons">
                                <font-awesome-icon icon="fa-solid fa-eye" class="icons"/>
                            </router-link>
                            </div>
                        </div>
                </div>
            </div>
        </slick>
    </div>
</template>

<script>
import '@/assets/styles/Box/BoxSlider/SliderAccessory.css';

import jsonDataTerra from '@/components/apiDataOffice/dataAccessory.json';
import Slick from 'vue-slick';
import 'slick-carousel/slick/slick.css';

export default{
    name: 'SliderFlower',
    components: { Slick },
    data: () => ({
        // carousel settings
        terra: jsonDataTerra,
        myOptions: {
                slidesToShow: 5,
                autoplay: true,
                autoplaySpeed: 5000,
                arrows: true,
                prevArrow: '<span type="button" class="slick-prev"><span class="material-symbols-sharp prev">chevron_left</span></span>',
                nextArrow: '<span type="button" class="slick-next"><span class="material-symbols-sharp next">chevron_right</span></span>',
                dots: false
           }
    }),
    methods: {
       
    },
}
</script>

