<template>
    <div class="register-main">
        <h1>Register</h1>
        <form @submit.prevent="Register" class="form-register">
            <input type="text" placeholder="Email" class="item email" v-model="email" />
            <input type="password" placeholder="Password" class="item" v-model="password"/>
            <input type="submit" value="Register" class="submit"/>
            <p class="account">Have an account? <router-link to="/login" class="register">Login Here</router-link></p>
        </form>
    </div>
</template>

<script>
import {ref} from 'vue';
import firebase from 'firebase/compat/app';
export default {
    name: 'RegisterModal',
    setup() {
        const email = ref("");
        const password = ref("");

        const Register = () => {
            firebase
                .auth()
                .createUserWithEmailAndPassword(email.value, password.value)
                .then(user => {
                    alert(user);
                })
                .catch(err => alert(err.message));
        }
        return{
            Register,
            email,
            password
        }
    }
}
</script>

<style>
.register-main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: rgb(80, 79, 79);
    height: 720px;
}
h1{
    color: #fff;
    font-size: 30px;
    margin-bottom: 10px;
}
.form-register{
    border: 2px solid #fff;
    display: flex;
    flex-direction: column;
    width: 400px;
    height: 300px;
    align-items: center;
}
.item.email{
    margin-top: 25px;
}
.item{
    margin-top: 10px;
    width: 250px;
    padding: 10px 20px;
    outline: none;
}
.submit{
    padding: 10px 35px;
    margin-top: 20px;
    background-color: aqua;
    cursor: pointer;
    border: none;
}
.submit:hover{
    background-color: rgb(11, 170, 170);
}
.account{
    color:  #fff;
    margin-top: 20px;
}
.register{
    margin-left: 10px;
    text-decoration: none;
    color: rgb(121, 206, 219);
}

</style>